body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  position: fixed;
  width: 100vw;
  height: 100vh;
  opacity: .8;
  /* z-index: -1; */
}

.App-header {
  background-color: #272334;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  text-align: center;
}

input{

  position: relative;
}

.title{
  font-size: 64pt;
  font-weight: 100;
  letter-spacing: -3px;
  text-align: center;
  width: 100%;
  padding: 20px 0px;
  position: relative;
  -webkit-animation: fade-in-down 1s forwards;
          animation: fade-in-down 1s forwards;
  -webkit-animation-delay: .5s;
          animation-delay: .5s;
  opacity: 0;
}

@-webkit-keyframes fade-in-down {
  from {
    opacity: 0;
    -webkit-transform: translateY(-15px);
            transform: translateY(-15px);
  }
  to {
    opacity: 1;
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
  }
}

@keyframes fade-in-down {
  from {
    opacity: 0;
    -webkit-transform: translateY(-15px);
            transform: translateY(-15px);
  }
  to {
    opacity: 1;
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
  }
}

.titleBlue{
  color: #B80F42;
  fontWeight: 500;
  fontSize: .7em;
  verticalAlign: 20%;
  position: relative;
  -webkit-animation: fade-in-down 1s forwards;
          animation: fade-in-down 1s forwards;
  -webkit-animation-delay: .5s;
          animation-delay: .5s;
  opacity: 0;
}

.cardContainer{
  display: inline-block;
  width: 100%;
  margin-top: 30px;
  position: relative;
}

.card{
  display: inline-block;
  width: 250px;
  padding: 10px;
  margin: 10px;
  background: #ddd;
  color: #333;
  text-align: center;
  border-radius: 20px;
  box-shadow: 3px 3px 20px -2px #222;
  -webkit-transition: all .5s ease;
  transition: all .5s ease;
  opacity: 0;
  -webkit-animation: fade-in-down 2s forwards;
          animation: fade-in-down 2s forwards;
  -webkit-animation-delay: 1s;
          animation-delay: 1s;

}

.card:hover{
  -webkit-transform: scale(1.02);
          transform: scale(1.02);
  opacity: 1;
  background: #fff;
  color:#B80F42;
  box-shadow: 3px 3px 40px 0px #111;
}

.cardHeader{
  font-size: 16pt;
  font-weight: 900;
  margin-bottom: 5px;
}

.cardBody{
  font-size: 12pt;
  font-weight: 300;
  line-height: 1.8;
}

.fileContainer {
    overflow: hidden;
    position: relative;
}

.fileContainer [type=file] {
    cursor: inherit;
    display: block;
    font-size: 999px;
    filter: alpha(opacity=0);
    min-height: 100%;
    min-width: 100%;
    opacity: 0;
    position: absolute;
    right: 0;
    text-align: right;
    top: 0;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.buttonPrimary{
  position: relative;
  color: white;
  border-radius: 40px;
  border: 0;
  padding: 14px 30px;
  width: calc(100% - 20px);
  font-weight: 500;
  font-size: 18px;
  letter-spacing: 2px;
  margin: 32px 10px;
  max-width: 400px;
  -webkit-transition: all .2s;
  transition: all .2s;
  background: #B80F42;
}

.buttonPrimary:hover{
  -webkit-transform: scale(1.03);
          transform: scale(1.03);
  box-shadow: 0px 5px 30px -3px #000;
  cursor: pointer;
}

.buttonPrimary:active{
  -webkit-transform: scale(1.02);
          transform: scale(1.02);
  box-shadow: 1px 1px 0px -10px #ccc;
  cursor: pointer;
}

